.welcome-text .btn-group {
  float: right;
}

.welcome-text h2 {
  color: #8492af;
  /* float: left; */
  font-size: 21px;
  font-weight: 600;
  margin: 0;
  padding: 0 0 0;
  line-height: 1.5;
}

.welcome-text .btn-group.open .dropdown-toggle,
.btn.active,
.btn:active {
  box-shadow: none;
}

.welcome-text .dropdown-menu>a {
  color: rgb(0, 78, 0);
  display: inline-block;
  font-weight: 800;
  padding: 9px 0;
  text-align: center;
  width: 100%;
}

.welcome-text .dropdown-menu>a:hover {
  color: limegreen;
}

@media only screen and (max-width: 767px) {
  .user-dashboard .welcome-text h2 {
    color: #8492af;
    /* float: left; */
    font-size: 16px;
    font-weight: 600;
    margin: 0;
    padding: 0 0 0;
  }

  .user-dashboard .btn.btn-secondary.btn-lg.dropdown-toggle>span {
    font-size: 11px;
  }

  /* .user-dashboard .welcome-text button {
          font-size: 11px;
          padding-right: 23px;
      } */

  .user-dashboard .welcome-text h2 {
    font-size: 14px;
  }

  .user-dashboard h1 {
    font-size: 25px;
  }

  .gutter {
    padding: 0;
  }
}

.text-justify {
  text-align: justify !important;
}

@media screen and (min-width: 100px) {
  .widthAdjust {
    width: 100%;
  }
}

.register-now {
  background: limegreen none repeat scroll 0 0;
  border-radius: 100px;
  color: #ffffff;
  font-size: 14px;
  font-weight: 600;
  padding: 10px 20px 10px 20px;
  position: relative;
  margin-right: 20px;
}

@media screen and (max-width: 767px) {
  .register-now::before {
    background: rgba(0, 0, 0, 0) no-repeat scroll 0 0;
    content: '';
    height: 12px;
    left: 17px;
    position: absolute;
    top: 12px;
    width: 12px;
  }

  .register-now:hover {
    color: #ffffff;
    background: forestgreen none repeat scroll 0 0;
  }

  .register-now:focus {
    color: #ffffff;
  }
}

@media screen and (max-width: 767px) {
  .register-now {
    font-size: 10px;
  }

  .overHandle {
    overflow: scroll;
  }
}

.register-now {
  border: 1px solid limegreen;
}

