.regn_Pad {
  margin-top: 20px;
}

.events-heading {
  font-family: 'League Spartan';
  text-align: center;
  color: cornflowerblue;
  font-size: 24px;
  padding-bottom: 10px;
}

.events-table {
  font-size: 17px;
}

@media only screen and (max-width: 767px) {
  .events-table {
    font-size: 12px;
  }
}

@media only screen and (min-width: 767px) and (max-width: 998px) {
  .events-table {
    font-size: 14px;
  }
}

.events-table th.middle-column {
  max-width: 55vw;
  min-width: 55vw;
}

.events-table .left-column {
  padding-right: 10px;
  line-height: 1;
  min-width: 215px;
}

.events-table .right-column {
  padding-left: 10px;
}

.events-table .middle-column {
  padding-left: 10px;
  padding-right: 10px;
}

.events-table th,
td {
  padding: 10px;
}

.events-table tr th {
  color: white;
}

.events-table tr td {
  font-weight: 500;
  color: #616f8a;
}

.events-table {
  font-family: 'Trebuchet MS', Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 100%;
}

.events-table td,
.events-table th {
  border: 1px solid #ddd;
  padding: 8px;
}

.events-table tr:nth-child(even) {
  background-color: #f2f2f2;
}

.events-table tr:hover {
  background-color: #ddd;
}

.events-table th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: darkslategray;
  color: white;
}

@media only screen and (max-width: 426px) {
  .events-table .right-column {
    min-width: 208px;
  }
}

@media only screen and (max-width: 376px) {
  .events-table .right-column {
    min-width: 180px;
  }
}

@media only screen and (max-width: 320px) {
  .events-table .right-column {
    min-width: 150px;
  }
}